@import "./src/assets/sass/prepends.scss";

.card-wrapper {
  padding: 1rem;

  .oauth-card {
    width: 100%;
    padding: .75rem;

    .oauth-card-row {
      display: flex;
      align-items: center;
      margin-bottom: 1rem;

      span.badge {
        display: inline-block;
        padding: .5rem;
        margin: .125rem 1.5rem;
        border-radius: .5rem;

        &.true {
          background-color: $bg-light-lighten3;
        }
        &.false {
          background-color: $bg-secondary-light;
        }
      }
    }
  }
}

#livelog {
  img {
    height: 3rem;
    width: auto;
  }
}
#google {
  img {
    height: 3rem;
    width: auto;
  }
}
